@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
* {
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
}

body{
    
    font: 400 14px 'Poppins', sans-serif;
    background: #f0f0f5;
    -webkit-font-smoothing: antialiased;
}

input, button, textarea{
    font: 400 18px 'Poppins', sans-serif;
}

button{
    cursor: pointer;
}

form input{
    width: 100%;
    height: 60px;
    color:#333333;
    border: solid 1px #dcdce6;
    border-radius: 8px;
    padding: 0 24px;
}

form textarea{
    width: 100%;
    min-height: 140px;
    color:#333333;
    border: solid 1px #dcdce6;
    border-radius: 8px;
    padding: 16px 24px;
    line-height: 24px;
    resize: vertical;
}

.button{
    width: 100%;
    height: 60px;
    background-color: #e02041;
    border: 0;
    border-radius: 8px;
    color:#fff;
    font-weight: 700;
    margin-top: 16px;
    display: inline-block;
    text-align: center;
    text-decoration: none;
    font-size: 18px;
    line-height: 60px;
    transition: -webkit-filter 0.2s;
    transition: filter 0.2s;
    transition: filter 0.2s, -webkit-filter 0.2s;
}
.button:hover{
    -webkit-filter: brightness(90%);
            filter: brightness(90%);
}

.back-link{
    display:flex;
    align-items: center;
    margin-top: 40px;
    color: #41414d;
    font-size: 18px;
    text-decoration: none;
    font-weight: 500;
    transition: opacity 0.2s;
}
.back-link svg{
    margin-right: 8px;
}
.back-link:hover{
    opacity: 0.8;
}
.top-header{
    width: 100%;
    height: 500px;
    background-image: url(/static/media/bg-progcientifica.31c60f0e.jpg);
    background-position: bottom center;
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.top-header h1{
    text-transform: uppercase;
    color: #fff;
    font-weight: 700;
    font-size: 40px;
    margin-left: 50px;
    margin-top: 40px;
}
.top-header h1 span{
    color:red;
}

/* ----- EVENT INFOS ----- */

.card-header{
    color: #0e1783;
    font-weight: 900;
    border: none;
    cursor:pointer;
}
.card-header .event-day{
    display: block;
    width: auto;
    float: left;
    background-color: #0072bc;
    color: #fff;
    font-weight: 900;
    margin: -12px 10px 0px -20px;
    padding: 11px;
    border: solid 1px #0072bc;
    text-transform: uppercase;
}

.card-body{
    /* background-color: #ececf4; */
    background-color: #ffffff;
}
.event-item{
    background-color: #ffffff;
    padding: 10px;
    margin-bottom: 10px;
    border-bottom: solid 1px #ececf4;
}

.event-item .speaker-infos, .event-item .speaker-infos img{
    width: 100%;
}
.event-item .speaker-infos span{
    position: relative;
    display: inline-block;
    background-color: red;
    font-weight: 900;
    color: #fff;
    padding: 10px;
    width: auto;
    top: -30px;
    left: -5px;
    margin-bottom: -30px;
    font-size: 12px;
}
.event-infos{
    color:#9698b6;
}
.event-infos h2{
    text-transform: uppercase;
    color: #0e1783;
    font-size: 30px;
    font-weight: 900;
}
.event-infos .finding-infos{
    font-size: 12px;
    margin-bottom: 20px;
    color: red;
    font-weight: 700;
}
.event-infos .finding-infos .evIcon{
    margin-right: 10px;
    margin-top:-3px;
}
.highlight{
    color: red;
}
